body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App {
  background: rgb(219, 219, 219);
  margin: 20px auto;
  width: 90%;
  max-width: 800px;
}

.navbar {
  padding: 5px;
  text-align: center;
}

.navbar ul {
  display: flex;
  justify-content: space-around;
  align-items: center;
  list-style: none;
  padding: 0;
}

.todo-list ul {
  padding: 0 10px 10px 10px;
  list-style-type: none;
}

.todo-list li {
  padding: 10px;
  cursor: pointer;
  margin: 10px 0;
  border-radius: 4px;
}

.todo-list li:hover {
  opacity: 0.7;
  text-decoration: line-through;
}

.todo-list li .bi-trash:hover {
  color: rgb(255, 0, 0);
}


form {
  padding: 10px;
}

input[type="text"] {
  width: 100%;
  box-sizing: border-box;
  padding: 10px;
  margin: 6px 0;
  border: 0;
}

input[type="submit"] {
  margin: 10px auto;
  padding: 10px;
  border: 0;
  display: block;
  cursor: pointer;
  border-radius: 15%;
}

input[type="submit"]:hover{
  background-color: orange;
}

.toggle-btn {
  text-align: center;
  padding: 10px auto;
}

button{
  border: 0;
  padding: 10px;
  margin: 10px auto;
}

.bi-trash{
  display: float;
  float: right;
  color: rgb(245, 69, 69)
}
